$block: 'contact-form';

.#{$block} {
	padding: 5rem 0;

	h1 {
		font-size: 2rem;
		font-weight: $font-weight-normal;
		margin-bottom: 2rem;
	}

	address {
		margin-bottom: 2rem;
	}

	&__row {
		@include media-min(md) {
			display: flex;
			flex-direction: row;
			justify-content: space-around;
		}
	}

	&__column {
		&:first-child {
			margin-bottom: 3rem;

			@include media-min(md) {
				margin-bottom: 0;
			}
		}
	}

	&__group {
		margin-bottom: 2rem;

		input[type='text']:first-child {
			margin-bottom: 2rem;

			@include media-min(md) {
				margin-bottom: 0;
				margin-right: 1rem;
			}
		}
	}

	input[type='text'],
	input[type='email'] {
		box-sizing: border-box;
		font-size: 1rem;
		height: 50px;
		padding: 0.5rem;
		width: 350px;
	}

	textarea {
		height: 175px;
		width: 100%;
	}
}
