@import '../reference/reference';

$block: 'social-icon';

.#{$block} {
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: 32px 32px;
    border: 0;
    box-shadow: none !important;
    display: block;
    font: 0 / 0 a;
    height: 32px;
    text-decoration: none;
    text-shadow: none;
    width: 32px;

    &--linkedin {
        background-image: url('/images/icons/linkedin.png');
    }
}
