$block: 'feature-panel';

.#{$block} {
    background: #f4f4f4;
    padding: 4rem 0 6rem;

    h1 {
        text-align: center;
    }

    &__wrapper {
        display: grid;
        grid-gap: 4rem 2rem;
        grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
        padding-top: 4rem;
    }

    &__item {
        align-items: center;
        display: flex;
        flex-direction: column;

        h2 {
            margin: 2rem 0;
        }

        &--description {
            text-align: center;
        }
    }

    &__picture {
        height: 40px;
    }
}
