@import '../reference/reference';

$block: 'masthead';

.#{$block} {
	.intro {
		&__container {
			padding: 3.5rem 5% 1rem 0;

			@include media-min(lg) {
				padding: 4rem 15% 4rem 0;
			}
		}

		&__heading {
			font-size: 1.875rem;
			font-weight: $font-weight-normal;
			line-height: 1.25;

			@include media-min(lg) {
				font-size: 4rem;
			}
		}
	}

	@include media-min(lg) {
		padding-top: 3rem;
	}

	&__container {
		box-sizing: border-box;
		overflow: hidden;
		position: relative;
	}

	&__picture-container {
		bottom: 0;
		height: auto;
		left: 0;
		margin-bottom: 0;
		margin-left: 0;
		margin-right: 0;
		max-height: 650px;
		min-height: 192px;
		position: absolute;
		right: 5vw;
		top: 0;

		@include media-min(lg) {
			right: 10vw;
		}
	}

	&__picture {
		height: 100%;
		left: 0;
		overflow: hidden;
		position: absolute;
		top: 0;
		width: 100%;
	}

	&__image {
		display: block;
		font-family: 'object-fit:cover';
		height: auto;
		min-height: 192px;
		object-fit: cover;
		object-position: center center;
		user-select: none;
		width: 100%;
	}

	&__content-container {
		background: $blue-regal url('/images/banners/masthead.png') no-repeat center right;
		color: $white;
		margin-top: 16.5%;
		padding-top: 15%;

		@include media-min(xxxl) {
			margin-top: 325px;
			padding-top: 325px;
		}

		h1 {
			color: $white;
		}

		.panel__subject {
			font-size: 0.875rem;
			font-weight: $font-weight-semibold;
			text-transform: uppercase;
		}
	}

	&__content-row {
		display: grid;
		padding: 6rem 0 5rem;

		@include media-min(md) {
			grid-template-columns: repeat(auto-fit, minmax(650px, 1fr));
			padding: 10rem 0;
		}
	}

	&__content-column {
		@include media-min(lg) {
			&:first-child {
				padding-right: 10rem;
			}

			&:last-child {
				padding: 0 5rem;
			}
		}
	}

	@include media-max(lg) {
		.stats-panel {
			&__row {
				grid-gap: 2rem 0;
				padding-top: 2rem;
				text-align: left;
			}

			&__column {
				text-align: left;
			}

			&__number {
				font-size: 1.5rem;
			}

			&__desc {
				font-size: 0.875rem;
			}
		}
	}
}
