@import '../reference/reference';

$block: 'case-studies';

.#{$block} {
	margin-bottom: 4rem;

	&__wrapper {
		display: grid;
		grid-gap: 4rem 2rem;
		grid-template-columns: repeat(1, 1fr);

		@include media-min(sm) {
			grid-template-columns: repeat(2, 1fr);
		}
	}
}

.case-study {
	h1,
	h4 {
		color: $grey-dark;
		text-decoration: none;
	}

	&__picture {
		width: 100%;
	}

	&__image {
		width: 100%;
	}

	&__name {
		align-items: center;
		display: flex;
		margin: 1rem 0;

		h1 {
			font-size: 1.5rem;
			font-weight: $font-weight-semibold;
			margin: 0 1rem 0 0;
		}
	}

	h4 {
		font-weight: $font-weight-normal;
	}

	a:focus,
	a:hover {
		h1 {
			color: $blue;
		}
	}
}

.casestudy-panel {
	background: #f4f4f4;
	padding: 6.5rem 0;

	&__container {
		@include media-min(md) {
			align-items: center;
			display: flex;
			flex-direction: row;
		}
	}

	&__content {
		padding: 0 0 0 7rem;

		h1 {
			margin-top: 0;
		}
	}
}
